<template>
    <XcateChannel :channel="channel"/>
</template>

<script>
import XcateChannel  from "../components/categories/XcateChannel.vue";

export default{
    data(){
        return {
            channel:0
        };
    },
    components:{
         XcateChannel
    },
    methods:{
        setChannel(){
            var route = this.$router.history.current.path;
            switch (route) {
                case "/categories/machines":
                    this.channel=0;
                    break;
                case "/categories/building":
                    this.channel=1;
                    break;
                case "/categories/automobile":
                    this.channel=2;
                    break;
                default:
                    this.channel=0;
            }
        }
    },
    mounted(){
        this.setChannel();
    },
    watch:{
        $route(){
            this.setChannel();
        }
    }
}
</script>

