<template>
    <div class="swiper">
      <div class="slider-content">
        <div  class="swiper-container">
          <div class=" swiper-wrapper" style="width: 1920px; transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1); transition-duration: 0ms; transform: translate(0px, 0px) scale(1) translateZ(0px);">
            <div v-for="n in data" class="  swiper-slide" style="width: 320px;">
              <div  class="slide-image" :style="{backgroundImage:`url(${n})`}"></div>
            </div>
          </div>
          <div class="swiper-pagination"></div>
        </div>
      </div>
    </div>
</template>





<script>
import Swiper from 'swiper';
import "swiper/dist/css/swiper.min.css";
import $ from "jquery";

export default{
    data(){
        return{
            data:[]
        }
    },
    computed:{
    },
    methods:{
        getDetailGoods(){
            let self=this;
            $.ajax({
                type:"get",
                url:"http://2267ru0331.iask.in/api/p_detail/",
                data:{
                  id:self.$route.query.id
                },
                success:function(response){
                    self.data=response.prslist.picture;
                    self.$nextTick(()=>{
                        self.initSwiper()
                     })
                    
                }
            })
        },
        initSwiper(){
                new Swiper ('.swiper-container', {
                loop: true,
                observer:true,//修改swiper自己或子元素时，自动初始化swiper
                observeParents:true,//修改swiper的父元素时，自动初始化swiper
                autoplay: {
                    disableOnInteraction: false,
                    delay: 2500,
                },
                preloadImages:true,
                pagination: {
                    el: '.swiper-pagination',
                }
            })
        }
    },
    mounted(){
       
        this.getDetailGoods();
    }
  
}
</script>












