function locateRouter() {
    //判断当前是什么路由
            this.footerBars.forEach(function(item){
                item.isCurrent=false;
            });
            var route = this.$router.history.current.path;
            switch (route) {
                case "/home":
                    this.footerBars[0].isCurrent=true;
                    break;
                case "/categories/machines":
                    this.footerBars[1].isCurrent=true;
                    break;
                case "/cart":
                    this.footerBars[2].isCurrent=true;
                    break;
                case "/user":
                    this.footerBars[3].isCurrent=true;
                    break;
                default:
                    this.footerBars[0].isCurrent=true;
            }
            this.$store.dispatch('setFooterBars',this.footerBars);
}

export default locateRouter