                   <i v-show="isCheckAll == false" class="iconfont  icon-checkbox-empty" @click="nocheckCurrent(item)"></i>
                   <i v-show="isCheckAll == true" class="iconfont icon-checkbox-fill" @click="checkCurrent(item)"></i>
                </div>
              </label>
              <div class="right">
                <div class="thumbnail">
                  <img data-src="//img1.yiwugou.com/i004/2018/08/20/48/6b7f7ae5907ed928356484766f144171.jpg" :src="item.picture" lazy="loaded">
                </div>
                <div class="content">
                  <div class="title" v-text="item.title"></div>
                </div>
              </div>
            </div>
            <div class="product-bd">
              <div class="item">
                <label class="left">
                  <div>
                  <!--
                    <i v-show="1==0" class="iconfont  icon-checkbox-empty"></i>
                    <i v-show="1==1" class="iconfont icon-checkbox-fill" ></i>
                 -->
                  </div>
                </label>
                <div class="right">
                  <div class="p-left">
                    <div class="property">尺码:M</div>
                    <div class="price-desc">
                      <span class="price" >{{'￥'+item.price}}</span>
                    </div>
                  </div>
                  <div class="p-right">
                    <div class="qauntity-control">
                      <span class="amount-icon amount-down" @click="decrease(item)">-</span>
                      <input type="number" maxlength="9" class="qauntity-input" v-model="item.num" >
                      <span class="amount-icon amount-up" @click="increase(item)">+</span>
                    </div>
                  </div>
                </div>
              </div>
