          <i class="iconfont" :class="isCheckAll? 'icon-checkbox-fill':'icon-checkbox-empty'" @click="checkAll"></i>
        </div>
      </div>
      <label for="allChecked">全选</label>
      <div  class="center">
        <div v-show="styleShow==true">
            <div  class="total-amount">合计:
              <span class="amount-num" >{{'￥'+totalMoney}}</span>
            </div>
            <div class="total-amount-desc">{{'共'+totalQty+'件,不含运费'}}</div>
        </div>
      </div>
      <div class="right" :class="{disable:false}" v-text="cartFt" @click="action"></div>
    </div>
    
  </div>
</div>
</template>

<script>
import Cookie from "../lib/cookie.js";
import $ from "jquery";

export default{
    data(){
        return {
            cartHd:"勾选结算",
            cartFt:"结算",
            styleShow:true,
            editLabel:1,
            isCheckAll:false,
            goodLists:[],
            totalQty:0,
            totalMoney:0,
            isCheckCur:false, //用于衡量点击单个商品  是发送点中ajax,还是未点中ajax
        }
    },
    methods:{
        //编辑和完成
        editShow(){
            this.styleShow = !this.styleShow;
            if(this.styleShow==true){
                this.cartHd="勾选结算";
                this.cartFt="结算";
                this.editLabel=1;
            }else if(this.styleShow==false){
                this.cartHd="勾选删除";
                this.cartFt="删除";
                this.editLabel=2;
            }
        },
        goBack(){
            this.$router.history.go(-1);
        },

        //商品的减法
        decrease(item){
            if(item.num>=1){
                let self=this;
                let uid=Cookie.getCookie("uid");
                $.ajax({
                    type:"get",
                    url:"http://2267ru0331.iask.in/api/goods_num_change/",
                    data:{
                      p_id:item.id,
                      u_id:uid,
                      plus_or_minus:0
                    },
                    success:function(response){
                       self.getUserCart();
                       self.getTotal();
                    }
                })
            }   
        },
        //商品的加法
        increase(item){
            let self=this;
            let uid=Cookie.getCookie("uid");
            $.ajax({
                type:"get",
                url:"http://2267ru0331.iask.in/api/goods_num_change/",
                data:{
                    p_id:item.id,
                    u_id:uid,
                    plus_or_minus:1
                },
                success:function(response){
                    self.getUserCart();
                    self.getTotal();
                }
            })
        },
        //当前用户的购物车
        getUserCart(){
            let self=this;
            let uid=Cookie.getCookie("uid");
            $.ajax({
                type:"get",
                url:"http://2267ru0331.iask.in/api/user_pr/",
                data:{
                  u_id:uid,
                  status:1
                },
                success:function(response){
                    self.goodLists=response.prslist;
                    //console.log(response);
                }
            })
        },
        //获得总价
        getTotal(){
            let len=this.goodLists.length;
            //console.log("增加或者减少之后");
            console.log(this.goodLists);
            this.totalQty=0;
            this.totalMoney=0;
            for(let i=0;i<len;i++){
                let cur=this.goodLists[i];
                let singleTotal=Number(cur.price)*cur.num;
                this.totalQty +=cur.num;
                this.totalMoney +=singleTotal;
            }
        },
        //全选或全不选
        checkAll(){
            this.isCheckAll = !this.isCheckAll;
            if(this.isCheckAll==true){
                this.getTotal();
            }
            if(this.isCheckAll==false){
                this.totalQty=0;
                this.totalMoney=0;
            }
        },
        //单选选中（未做,未与后端提前沟通好，也未提供删除商品对应的接口）
        checkCurrent(item){
            let self=this;
            let uid=Cookie.getCookie("uid");
            $.ajax({
                type:"get",
                url:"http://2267ru0331.iask.in/api/cart_select/",
                data:{
                  p_id:item.id,
                  u_id:uid,
                  status:1
                },
                success:function(){
                   //console.log(response);
                }
            })
        },
        //单选未选中（未做,未与后端提前沟通好，也未提供删除商品对应的接口）
        nocheckCurrent(item){
            let self=this;
            let uid=Cookie.getCookie("uid");
            $.ajax({
                type:"get",
                url:"http://2267ru0331.iask.in/api/cart_select/",
                data:{
                  p_id:item.id,
                  u_id:uid,
                  status:0
                },
                success:function(){
                    //console.log(response);
                }
            })
        },
        //跳转到订单页
        action(){
            if(this.cartFt=="结算"){
               this.$router.push({name:"order"});
            }
        },
        isLogin(){
            let uid=Cookie.getCookie("uid");
            if(uid==undefined){
                this.$router.push({name:"login"});
            }
        }
    },
    mounted(){
        this.isLogin();
        this.getUserCart();
    },
}
</script>

