<template>
<div>
    <div style="height: 100px;"></div>
    <div class="hot-keywords">
        <div class="title">热搜</div>
     </div>

    <div v-show="searchTabShow==true" class="history-keywords">
        <div class="gutter"></div>
        <div class="title">历史搜索</div>
        <ul class="keywords">
          <li @click="getResult(item)" v-for="(item,index) in historyLists" :key="index" class="keyword" v-text="item.title"></li>
          
        </ul>
        <div  class="btn-area">
          <button @click="clearHistory" class="clear-btn">清空搜索历史</button>
        </div>
    </div>
    <ul class="suggest-keywords" style="display: none;"></ul>
</div>
</template>


<script>
export default{
    data(){
        return {
        }
    },
    computed:{
        searchTabShow(){
            return  this.$store.getters.getSearchTabShow;
        },
        historyLists(){
            return  this.$store.getters.getHistoryLists;
        }   
    },
    methods:{
        clearHistory(){
            this.$store.dispatch('setHistoryLists',[]);
        },
        getResult(item){
            this.$store.dispatch('setFindKey',item.title);
            this.$router.push({
                name:'result',
                query:{
                    q:item.title
                }
            });
        }
    }
}
</script>

