<template>
<div>
    <div class="searchbar">
        <a @click="goBack" class="title" href="javascript:;"><i class="iconfont icon-back"></i></a>
        <div @click="goSearch" class="search" >
          <span class="text">请输入关键字</span>
          <i class="iconfont icon-search"></i>
        </div>
        <div @click="showNavs" class="right" id="menuHook"><i class="iconfont icon-more"></i>
        </div>
    </div>

    <transition name="fade">
     <div v-show="nav===true" class="nav" >
        <a v-for="(n,index) in footerBars" :key="index" :class="setCurrent(n)" @click="setRouter(n)" class="item" href="javascript:;" >
          <i class="iconfont " :class="setClass(n)"></i>
          <div class="title" v-text="n.title"></div>
        </a>
    </div>
    </transition>
</div>
</template>

<script>
import locateRouter from "../../lib/locateRouter.js";
export default{
    data(){
        return {
            nav:false
        }
    },
    computed:{
        footerBars(){
            return  this.$store.getters.getFooterBars;
        }
    },
    methods:{
        locateRouter,
        showNavs(){
            this.nav = !this.nav;
        },
        goSearch(){
            this.$router.push({name:"search"});
        },
        setClass(n){
            let obj={};
            obj[n.iconName]=true;
            return obj;
        },
        setCurrent(n){
            let obj={};
            obj.current=n.isCurrent;
            return obj;
        },
        goBack(){
            this.$router.history.go(-1);
        },
        setRouter(n){
            this.$router.push({name:n.path});
            
            this.locateRouter();
        }
    },
    mounted(){
        this.locateRouter();
    },
    //watch:{
        //$route(){
            //this.setRouter();
        //}
    //}
}
</script>
