<template>
    <form  class="searchbar">
        <a @click="goHome" href="javascript:;" class="title">
          <i class="iconfont icon-back"></i>
        </a>
        <div class="search">
          <input @blur="getValue" ref="keyword" name="keyword" placeholder="请输入关键字"  autofocus="autofocus" class="search-input">
          <i class="iconfont icon-search"></i>
        </div>
        <div @click="changeHistoryLists(q)" id="searchBtnHook" class="right">搜索</div>
    </form>
</template>

<script>
export default{
    data(){
        return {
            q:""
        }
    },
    computed:{
        historyLists(){
            return  this.$store.getters.getHistoryLists;
        }   
    },
    methods:{
        getValue(){
            this.q=this.$refs.keyword.value;
        },
        changeHistoryLists(q){
            //添加进搜索记录,防止重名
            var res=this.historyLists.every((item)=>{
                return item.title != this.q;
            });
            if(res==true){
                this.historyLists.unshift({title:q,path:""});
                this.$store.dispatch('setHistoryLists',this.historyLists);
            }
            //设置一下差找的参数
            this.$store.dispatch('setFindKey',q);

            //跳转至结果页
            this.$router.push({
                name:'result',
                query:{
                    q:q
                }
            });
            
        },
        goHome(){
            this.$router.push({name:'home'});
        }
    }
}
</script>

