import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false

// vue-router
import VueRouter from 'vue-router'
Vue.use(VueRouter)
// vuex
import Vuex from 'vuex'
Vue.use(Vuex)


//一级路由
import Home from "./containers/Home.vue";
import Search from "./containers/Search.vue";
import Result from "./containers/Result.vue";
import Detail from "./containers/Detail.vue";
import Login from "./containers/Login.vue";
import Categories from "./containers/Categories.vue";
import Cart from "./containers/Cart.vue";
import Resgiter from './containers/Resgiter.vue';
import User from './containers/User.vue';
import Order from './containers/Order.vue';


//二级路由
//import ResultChannel from "./containers/ResultChannel.vue";
import CategoriesChannel from "./containers/CategoriesChannel.vue";


const routes = [{
          path:'/home',
          component: Home,
          name: "home",
        },{
          path:'/search',
          component: Search,
          name: "search",
        },{
          path:'/result',
          component: Result,
          name: "result",
          //children:[{
                    //path:'good',
                    //component:ResultChannel,
                    //name: "good"
                  //},{
                    //path:'shop',
                    //component:ResultChannel,
                    //name: "shop"
                  //}]
        },{
          path:'/detail',
          component: Detail,
          name: "detail",
        },{
          path:'/login',
          component: Login,
          name: "login",
        },{
          path:'/categories',
          component: Categories,
          name: "categories",
          children:[{
                  path:'machines',
                  component: CategoriesChannel,
                  name: "machines",
                },{
                  path:'building',
                  component: CategoriesChannel,
                  name: "building",
                },{
                  path:'automobile',
                  component: CategoriesChannel,
                  name: "automobile",
                }]
        },{
          path:'/cart',
          component: Cart,
          name: "cart",
        },{
          path: '/resgiter', 
          component: Resgiter,
          name:"resgiter"
        },{
          path: '/user', 
          component: User,
          name:"user"
        },{
          path: '/order', 
          component: Order,
          name:"order"
        },{
          path: '/',
          redirect: '/home'
        }]

const router = new VueRouter({
  routes // (缩写) 相当于 routes: routes
})

// 组件1 -----> actions(下命令) ------> mutatios(改) ------> state(仓库) ---->getter(拿) ------>  组件2
const store = new Vuex.Store({
  // 状态
  state:{
    uid:"",
    //查找的参数
    findKey:"",
    //商品还是商店
    resultChannel:"good",
    searchTabShow:true,
    historyLists:[{
      title:"裤",
      path:""
    },{
      title:"纸",
      path:""
    }],
    footerBars:[{
      title:"首页",
      iconName:"icon-home",
      imgsrc:"//static.yiwugou.com/assets/home.png",
      onsrc:"//static.yiwugou.com/assets/home_on.png",
      path:"home",
      isCurrent:false
    },{
      title:"分类",
      iconName:"icon-searchlist",
      imgsrc:"//static.yiwugou.com/assets/cat.png",
      onsrc:"//static.yiwugou.com/assets/cat_on.png",
      path:"machines",
      isCurrent:false
    },{
      title:"购物车",
      iconName:"icon-cart",
      imgsrc:"//static.yiwugou.com/assets/cart.png",
      onsrc:"//static.yiwugou.com/assets/cart_on.png",
      path:"cart",
      isCurrent:false
    },{
      title:"我的",
      iconName:"icon-profile",
      imgsrc:"//static.yiwugou.com/assets/profile.png",
      onsrc:"//static.yiwugou.com/assets/profile_on.png",
      path:"user",
      isCurrent:false
    }]
  },
  // 修改状态
  mutations:{
    editSearchTabShow(state,data){
      state.searchTabShow=data;
    },
    editHistoryLists(state,data){
      state.historyLists=data;
    },
    editFooterBars(state,data){
      state.footerBars=data;
    },
    editFindKey(state,data){
      state.findKey=data;
    },
    editResultChannel(state,data){
      state.resultChannel=data;
    },
    editUid(state,data){
      state.uid=data;
    },
  },
  // actions  一般配合 事件@xxx 触发
  actions:{
    setSearchTabShow(context,data){
      context.commit('editSearchTabShow',data);
    },
    setHistoryLists(context,data){
      context.commit('editHistoryLists',data);
    },
    setFooterBars(context,data){
      context.commit('editFooterBars',data);
    },
    setFindKey(context,data){
      context.commit('editFindKey',data);
    },
    setResultChannel(context,data){
      context.commit('editResultChannel',data);
    },
    setUid(context,data){
      context.commit('editUid',data);
    }
  },
  // 组件从store(中介)手上拿数据  配个 computed
  getters:{
    getSearchTabShow:state=>{
      return state.searchTabShow;
    },
    getHistoryLists:state=>{
      return state.historyLists;
    },
    getFooterBars:state=>{
      return state.footerBars;
    },
    getFindKey:state=>{
      return state.findKey;
    },
    getResultChannel:state=>{
      return state.resultChannel;
    },
    getUid:state=>{
      return state.uid;
    },
  }

})




new Vue({
  router, //注入路由功能
  store, //注入了仓库功能
  render: h => h(App),
}).$mount('#app')
