<template>
<div>
  <div class="order-create">
    <div id="selectAddress" class="addressContainer">
      <div class="addressBox noAddress" onclick="location.href='http://wap.yiwugo.com/user/addresses/create'">
        <input type="hidden" value="" name="oaid" />
        <div class="receiveDetail">
          <i class="icon-location"></i>请添加您的收货地址</div>
      </div>
      <b class="border"></b>
      <span class="point"></span>
    </div>
    <div class="itemContainer">
      <div class="hd">购物清单</div>
    <div v-for="(item,index) in goodLists" :key="index" class="itemBox">
        <div class="header">
          <h3 class="sellerInfo">订单:不是我不渲染,是没有这个数据==！</h3></div>
        <div class="content">
          <div class="good">
            <div class="goodId" style="display: none;">927895872</div>
            <dl class="goodInfo" @click="goDetail(item)">
              <dt>
                <a href="javascript:0">
                  <img :src="item.picture[0]" alt="蘑菇钉 拼插板组合儿童益智DIY拼装玩具厂家直销" /></a>
              </dt>
              <dd class="description">
                <div>
                  <a href="javascript:;" v-text="item.title"></a></div>
              </dd>
            </dl>
            <ul class="goodDetail">
              <li>
                <div class="single">
                  <div class="meta meta-sku">
                    <span class="sku">无规格</span>
                    <span class="unit-price">
                      <em class="fd-cny">单价：&yen;</em>
                      <em class="cash" v-text="item.price"></em></span>
                  </div>
                  <div class="meta meta-amount">
                    <div class="amount">
                      <span>数量：{{item.num}}</span></div>
                    <div class="singleTotalFee">
                    <!--
                      <em class="fd-cny">&yen;</em>
                      <em class="cash" >{{单个商品的总价}}</em>
                      -->
                    </div>
                  </div>
                </div>
              </li>
            </ul>
            <div class="freight" style="display:none;">运费：
              <span></span></div>
          </div>
        </div>
        <div class="footer">
          <div class="freightType"></div>
          <div class="totalFee">小计 (
            <span class="freightTxt">运费￥0.00</span>) :
            <span class="totalAmount">&yen;
              <em>52.00</em>
            </span>
          </div>
        </div>
    </div>
    </div>
    <div class="bottomBar">
      <div class="content">
        <div class="total">合计 &yen;
          <em class="addFreight">{{totalMoney}}</em>
          <em class="freightTxt">(运费0.00)</em></div>
        <button id="submitOrderBtn" class="button-submit" type="button" @click="goUser">提交订单</button></div>
    </div>
  </div>
  <div id="addressPopup" class="weui-popup-container">
    <div class="weui-popup-overlay"></div>
    <div class="weui-popup-modal">
      <div class="addressListBox">
        <ul class="addressList"></ul>
      </div>
      <div style="height: 42px;"></div>
      <a class="weui_btn weui_btn_primary" id="addAddress" href="http://wap.yiwugo.com/user/addresses/create">添 加 地 址</a></div>
  </div>
</div>
</template>




<script>
import Cookie from "../lib/cookie.js";
import $ from "jquery";

export default {
    data(){
        return {
            goodLists:[],
            totalMoney:0
        }
    },
    methods:{
        //获得当前用户的所选商品
        getUserCart(){
            let self=this;
            let uid=Cookie.getCookie("uid");
            $.ajax({
                type:"get",
                url:"http://2267ru0331.iask.in/api/user_pr/",
                data:{
                  u_id:uid,
                  status:1
                },
                success:function(response){
                    self.goodLists=response.prslist;
                    self.getTotal();
                }
            })
        },
        getTotal(){
            let len=this.goodLists.length;
            for(let i=0;i<len;i++){
                let cur=this.goodLists[i];
                let singleTotal=Number(cur.price)*cur.num;
                this.totalMoney +=singleTotal;
            }
        },
        goDetail(item){
            this.$router.push({
                name:"detail",
                query:{
                    id:item.id
                }
            })
        },
        goUser(){
            this.$router.push({name:"user"});
        }
    },
    mounted(){
        this.getUserCart();
        
    },
}
</script>

