	left: 0;
	bottom: 0;
	width: 100%;
	height: 1px;
	border-top: 1px solid #d9d9d9;
	color: #d9d9d9;
	-webkit-transform-origin: 0 100%;
	transform-origin: 0 100%;
	-webkit-transform: scaleY(.5);
	transform: scaleY(.5)
}

.weui-popup-container,.weui-popup-overlay {
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
	z-index: 10
}

.weui-popup-overlay {
	background-color: rgba(0,0,0,.6);
	opacity: 0;
	-webkit-transition: opacity .3s;
	transition: opacity .3s
}

.weui-popup-container {
	display: none
}

.weui-popup-container.weui-popup-container-visible {
	display: block
}

.weui-popup-container .weui_cells {
	margin: 0;
	text-align: left
}

.weui-popup-modal {
	width: 100%;
	position: absolute;
	z-index: 100;
	bottom: 0;
	border-radius: 0;
	opacity: .6;
	color: #3d4145;
	-webkit-transition-duration: .3s;
	transition-duration: .3s;
	height: 100%;
	background: #EFEFF4;
	-webkit-transform: translate3d(0,100%,0);
	transform: translate3d(0,100%,0);
	-webkit-transition-property: opacity,-webkit-transform;
	transition-property: opacity,-webkit-transform;
	transition-property: transform,opacity;
	transition-property: transform,opacity,-webkit-transform;
	overflow-x: hidden;
	overflow-y: auto
}

.popup-bottom .weui-popup-modal {
	height: auto
}

.weui-popup-modal .toolbar {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	z-index: 1
}

.weui-popup-modal .modal-content {
